.mini-slider
  padding-top: 133px
  background-color: $black
  &__container
    display: flex
    align-items: flex-start
    flex-wrap: wrap

  &__intro
    flex-shrink: 0
    margin-right: 55px
    margin-left: -7px
    width: 311px
    height: 513px

  &__slider
    // flex-shrink: 0
    width: 700px
    height: 577px
    background-color: #fff
    position: relative
  &__slide
    width: 700px
    height: 577px
    // &::after
    //   content: ''
    //   position: absolute
    //   top: 0
    //   left: 0
    //   width: 100%
    //   height: 100%
    //   // background-color: $black
    //   opacity: .6
    //   z-index: 1
  &__btn
    position: absolute
    top: 50%
    transform: translateY(-50%)
    height: 22px
    width: 25px
    cursor: pointer
    z-index: 12
    &-prev
      left: 0
    &-fill
      width: 25px
      height: 22px
      fill: $dark-grey
      object-fit: cover
      transition: fill 0.3s ease-in-out
    &-next
      right: 0
      transform: rotate(180deg)

  &__button
    padding: 13px 48px 13px 25px
